.btn {
  display: inline-flex;
  padding: 24px;

  color: $color-default-white;
  text-transform: uppercase;
  font-family: $ff-roboto-condenced;
  font-weight: 700;
  letter-spacing: 0.05em;
  font-size: 14px;
  line-height: 16px;
  justify-content: center;

  background-color: $color-transparent;
  border: 4px solid $color-default-white;
  cursor: pointer;
  user-select: none;

  transition: opacity $trans-default;

  &:disabled {
    opacity: 0.5;

    pointer-events: none;
  }

  @include hover-focus {
    opacity: 0.7;
  }
}

.btn--header {
  font-size: 12px;
  line-height: 14px;
  padding: 20px;
  width: 233px;

  @include vp-1199 {
    width: 186px;
  }

  @include vp-767 {
    width: 100%;
  }
}

.btn--subscription {
  display: flex;
  align-items: center;
  font-size: 12px;
  line-height: 14px;
  color: $color-sapphire;
  max-width: 206px;
  width: 100%;
  padding: 23px;
  min-height: 64px;
  border: 2px solid $color-sapphire;
}

.btn--promo {
  width: 100%;
  font-size: 12px;
  line-height: 14px;

  @include vp-1199 {
    font-size: 10px;
    line-height: 12px;
    border-width: 2px;
    padding: 12px;
  }
}

.btn--coaches {
  padding: 0;
  border: 0;
  width: calc((100vw - 1160px)/2);
  height: 100%;
  z-index: 1;

  &-prev {
    position: absolute;
    top: 0;
    left: -40px;
  }
}
